import React, { useEffect, useState,useMemo } from 'react'
import { CustomButton } from '../../globalComponents'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import moment from 'moment-timezone'
import { toast } from 'react-toastify'
import { CustomSpinner } from '../../globalComponents'
import { useDispatch, useSelector } from 'react-redux'
import { getBadgeColor } from '../../../utils/common'
import CustomTable from '../../reuseableComponents/customTable/CustomTable'

const ShiftAddCalendar = (props) => {
    const { calendarList,isLoading,openAddCalendarModel,currentPage,setCurrentPage,setSelectedCalendarDatas,onHandleDeleteModal,onHandleViewModal} = props

    const dispatch = useDispatch();
    const [isOpen, setIsOpen] = useState(false)
    const [viewEmailTemplate, setViewEmailTemplate] = useState([])

      const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'title', label: 'Template Name' },
        { key: 'working_days', label: 'Working Days' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label :'Action' }
      ];

      const updateShiftCalendar = (data) => {
        setSelectedCalendarDatas(data)
        openAddCalendarModel()
      }

      const scopedSlots = useMemo(() => ({
        sn: (_, index) => (         
           <span>{index+1}</span> 
        ),
        is_active: item => (        
          <span
            className={`badge ${getBadgeColor(item.is_active? "Active":"Inactive")}`}
            // style={{ color: '#fff', padding: x'0.5rem' }}
          >
            {item.is_active? "Active":"Inactive"}
          </span>
        ),
        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
          <div className="actionGroup d-flex gap-2">
            {/* <CustomButton
              text="View"
              handleClick={() => onHandleViewModal(item)}
              classNames="btn btn-success"
            /> */}
            <CustomButton
              text="Update"
              classNames="btn btn-primary"
              handleClick={() => {
                updateShiftCalendar(item);
              }}
            />
            <CustomButton
              text="Delete"
              handleClick={() => onHandleDeleteModal(item?.id)}
              classNames="btn btn-danger"
            />
          </div>
        )
      }), [setSelectedCalendarDatas, openAddCalendarModel, onHandleDeleteModal,onHandleViewModal]);
  
  return (
    <>
    <CustomTable
        data={calendarList?.data}
        fields={fields}
        isLoading={isLoading}
        pagination={true}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        totalPages={calendarList?.totalPages}
        tableName={"Shift Calendar"}
        // onSearch={handleSearch}
        scopedSlots={scopedSlots}
        />
    </>
  )
}

export default ShiftAddCalendar
