import React, { useEffect, useState, useMemo } from 'react'
import { AddDesignations, DesignationTable } from '../components/admin/mapping/designation'
import Button from '../components/commanComponent/Button'
import { useDispatch, useSelector } from 'react-redux'
import { emailTemplateAction } from '../core/resource/actions'
import {shiftManagementAction} from '../core/resource/actions'
import { toast } from 'react-toastify'
import ShiftAddCalendar from '../components/admin/shiftManagement/ShiftAddCalendar'
import CreateEmailType from '../components/modal/admin/emailTemplate/CreateEmailType'
import AddCalendar from '../components/modal/shiftManagement/AddCalendar'
import { pagination } from '../utils/Constants.config'
import ApproveRejectModal from '../components/modal/ApproveRejectModal'
import TableTabs from '../components/TableTabs'
import AddActionEmailTemplate from '../components/modal/admin/emailTemplate/AddActionEmailTemplate'


const ShiftSetting = () => {
  const dispatch = useDispatch()
  const [openAddCalendar, setOpenAddCalendar] = useState(false);
  const {getAllShiftCalendarRes,getAllShiftCalendarError,getAllShiftCalendarPending, deleteShiftCalendarPending, deleteShiftCalendarRes, deleteShiftCalendarError} = useSelector(state => state.shift);
  const [currentPage, setCurrentPage] = useState(1)
  const [selectedCalendarData, setSelectedCalendarDatas] = useState('')
  const [selectedActionData, setSelectedActionData] = useState('')
  const [deleteCalId, setDeleteCalId] = useState()
  const [isShowDeleteModal, setIsShowDelelteModal] = useState(false)
  const [onHandleViewModals, setonHandleViewModal] = useState('')
  const [openAction, setOpenAction] = useState(false)
  const [AllGetAction, setAllGetAction] = useState([])
  const [actionDelete,setActionDelete] = useState()
  const [isActionDelete,setIsActionDelete] = useState(false)
  const [getAllCalendardata, setAllCalendarData] = useState([])

  useEffect(() => {
    if (!getAllShiftCalendarPending) {
        if (getAllShiftCalendarError) {
            setAllCalendarData([])
            return;
        }
        if (getAllShiftCalendarRes) {
            if (getAllShiftCalendarRes?.code == 200) { 
              setAllCalendarData(getAllShiftCalendarRes?.data)
            }
        }
    }
}, [
    getAllShiftCalendarPending,
    getAllShiftCalendarRes,
    getAllShiftCalendarError
]);

useEffect(() => {
    if (!deleteShiftCalendarPending) {
      if (deleteShiftCalendarError) {
            dispatch(shiftManagementAction.handleClearShiftStore('HANDLE_CLEAR_DELETE_SHIFT_CALENDAR_STORE'))
        return;
      }
      if (deleteShiftCalendarRes) {
        if (deleteShiftCalendarRes?.data?.code == 200) {
          dispatch(shiftManagementAction.handleClearShiftStore('HANDLE_CLEAR_DELETE_SHIFT_CALENDAR_STORE'))
          getAllShiftCalendar()
        }
      }
    }
  }, [
    deleteShiftCalendarRes,
    deleteShiftCalendarError,
    deleteShiftCalendarPending
  ]);

  const onHandleViewModal = (data) => {
    setonHandleViewModal(data)
    openAddCalendarModel()
  }

  const onHandleDeleteModal = (id) => {
    setIsShowDelelteModal(!isShowDeleteModal)
    setDeleteCalId(id)
  }

  const onHandleDeleteAction = id =>{
    setIsActionDelete(!isActionDelete)
    setActionDelete(id)
  }

  useEffect(() => {
    getAllShiftCalendar()
  }, [currentPage])

  const getAllShiftCalendar = () => {
    const params = {
      page: currentPage,
      limit: pagination.limit,
    }
    dispatch(shiftManagementAction.getAllShiftCalendarRequest(params))
  }

  const openAddCalendarModel = (type) => {
    setOpenAddCalendar(!openAddCalendar);
  };

  const toggleOpenAction = (type) => {
    setOpenAction(!openAction)
  };

  const onDeleteCancelCalendar = (type) => {
    if (type) {
      const params = {
        _id: deleteCalId
      }
      dispatch(shiftManagementAction?.deleteShiftCalendarRequest(params))
      setIsShowDelelteModal(!isShowDeleteModal)
    } else {
      setIsShowDelelteModal(!isShowDeleteModal)
    }
  }


  const memoizedCalendarList = useMemo(() => getAllCalendardata, [getAllCalendardata]);
  const memoizedActionList = useMemo(() => AllGetAction, [AllGetAction]);
  return (
      <div className="cardBox mb-3 mt-3 pt-5 myRequest">
        <ul className="nav nav-tabs leaveTabs mb-4">
          <li className="nav-item">
            <button
              className="nav-link active"
              data-bs-toggle="tab"
              data-bs-target="#createEmailTemplate"
              type="button"
            >
              Calendar
            </button>
          </li>
          <li className="nav-item">
            <button
              className="nav-link"
              data-bs-toggle="tab"
              data-bs-target="#addAction"
              type="button"
            >
              Shift Bulk Upload
            </button>
          </li>
        </ul>
        <div className="tab-content">
          <div className="tab-pane fade active show" id="createEmailTemplate">
            <div className="cardHead d-flex align-items-center justify-content-between">
              <Button btnClassName={"btn btn-primary gradientBg"}
                onClickFun={() => openAddCalendarModel()}
              >Add Calendar</Button>
            </div>
            <ShiftAddCalendar
              calendarList={memoizedCalendarList}
              isLoading={getAllShiftCalendarPending}
              openAddCalendarModel={openAddCalendarModel}
              setSelectedCalendarDatas={setSelectedCalendarDatas}
              onHandleDeleteModal={onHandleDeleteModal}
              currentPage={currentPage}
              setCurrentPage={setCurrentPage}
              onHandleViewModal={onHandleViewModal}
            />
            <AddCalendar
              openAddCalendar={openAddCalendar}
              openAddCalendarModel={openAddCalendarModel}
              setSelectedCalendarDatas={setSelectedCalendarDatas}
              selectedCalendarData={selectedCalendarData}
              getAllShiftCalendar={getAllShiftCalendar}
            />
          </div>
          <div className="tab-pane fade active" id="addAction">
            <div className="cardHead d-flex align-items-center justify-content-between">
              {/* <Button btnClassName={"btn btn-primary gradientBg"}
                onClickFun={() => toggleOpenAction()}
              >Add Action</Button> */}
            </div>
          </div>
        </div>
      {isShowDeleteModal && (
        <ApproveRejectModal
          onApproveReject={onDeleteCancelCalendar}
          isShow={isShowDeleteModal}
          desc={"You want to delete this Shift Calendar"}
          btnRightLoading={deleteShiftCalendarPending}
        />)
      }


    </div>
  )
}

export default ShiftSetting
