import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { CustomButton } from '../../globalComponents';
import { shiftManagementAction } from '../../../core/resource/actions';
import { toast } from 'react-toastify';
import { addCalendarchema } from '../../../utils/ValidationSchema';
import { validateSchema } from '../../../utils/ValidationHelper';
import CustomError from '../../globalComponents/CustomError';
import moment from 'moment'
import CustomHoursMinPicker from '../../globalComponents/CustomTimePicker';

const AddCalendar = (props) => {
    const dispatch = useDispatch();
    const { openAddCalendar, openAddCalendarModel, selectedCalendarData, setSelectedCalendarDatas, getAllShiftCalendar } = props
    const { addShiftCalendarPending, addShiftCalendarRes, addShiftCalendarError } = useSelector(state => state.shift)
    const [errors, setErrors] = useState({});
    const [calendarId, setCalendarId] = useState('')
    const [workingDay, setWorkingDay] = useState('')
    const [weekOff, setweekOff] = useState()
    const [alternativeSaturday, setalternativeSaturday] = useState([])
    const [addCalendar, setAddCalendar] = useState({
        title: '',
        working_days: '',
        alternative_saturday_off: '',
        week_off: ''
    })

    const setWorkingDayfun = (data) => {
        setWorkingDay(data)
        if (data == '5') {
            setweekOff(['6', '0'])
        } else if (data == '6') {
            setweekOff([])
            setweekOff(['0'])
        } else if (data == '0') {
            setweekOff([])
        }
    }
    const alternativeSaturdayFun = (e) => {
        if (e.target.checked == true) {
            setalternativeSaturday([...alternativeSaturday,e.target.value])
        } else {
            let newResult = alternativeSaturday.filter(val => val != e.target.value)
            setalternativeSaturday([...newResult])
        }
    }
    const weekOffFun = (e) => {
        if (e.target.checked == true) {
            setweekOff([...weekOff, e.target.value])
        } else {
            let newResult = weekOff.filter(val => val != e.target.value)
            setweekOff([...newResult])
        }
    }


    const handleChange = (event) => {
        const { name, value } = event.target;
        setAddCalendar({ ...addCalendar, [name]: value });
        setErrors(prev => ({ ...prev, [name]: undefined }));
    };

    useEffect(() => {
        if (!addShiftCalendarPending) {
            if (addShiftCalendarError) {
                toast.error(addShiftCalendarError?.errorMessage)
                return;
            }
            if (addShiftCalendarRes) {
                if (addShiftCalendarRes?.data?.code === 200) {
                    toast.success(addShiftCalendarRes?.data?.msg)
                    openAddCalendarModel()
                    dispatch(shiftManagementAction.handleClearShiftStore("HANDLE_CLEAR_ADD_SHIFT_CALENDAR_STORE"))
                }
            }
        }
    }, [
        addShiftCalendarRes,
        addShiftCalendarError,
        addShiftCalendarPending
    ]);

    useEffect(() => {
        setAddCalendar({
            title: selectedCalendarData?.title || '',
            working_days: selectedCalendarData?.working_days || '',
            alternative_saturday_off: selectedCalendarData?.alternative_saturday_off || '',
            week_off: selectedCalendarData?.week_off

        })
        setWorkingDay(selectedCalendarData?.working_days)
        setweekOff(selectedCalendarData?.week_off)
        setalternativeSaturday(selectedCalendarData?.alternative_saturday_off == undefined ? [] : selectedCalendarData?.alternative_saturday_off)
        setCalendarId(selectedCalendarData?.id)
        
    }, [selectedCalendarData])

    // const handleSubmitCreateShift = async () => {
    //     createShift.is_active = isActive
    //     const validationResponse = await validateSchema(addShiftSchema, createShift);
    //     if (validationResponse.isValid) {
    //         setErrors({});
    //         if (editShift) {
    //             createShift['_id'] = shiftId
    //             dispatch(shiftManagementAction.updateShiftRequest(createShift));
    //             openAddCalendarModel()
    //             getAllShiftFunction()
    //         } else {
    //             dispatch(shiftManagementAction.addShiftRequest(createShift));
    //             getAllShiftFunction()
    //         }

    //     } else {
    //         setErrors(validationResponse.errors);
    //         console.log(validationResponse.errors, "formattedErrors");
    //     }
    // }


    const handleSubmitCreateShiftConfiguration = async () => {
        addCalendar.working_days = workingDay
        addCalendar.alternative_saturday_off = alternativeSaturday
        addCalendar.week_off = weekOff
        const validationResponse = await validateSchema(addCalendarchema, addCalendar);
        if (validationResponse.isValid) {
            setErrors({});
            if (selectedCalendarData) {
                addCalendar['_id'] = calendarId
                dispatch(shiftManagementAction.updateShiftCalendarRequest(addCalendar))
                openAddCalendarModel()
                getAllShiftCalendar()
            } else {
                dispatch(shiftManagementAction.addShiftCalendarRequest(addCalendar))
                getAllShiftCalendar()
            }
            ;
        } else {
            setErrors(validationResponse.errors);
            // console.log(validationResponse.errors, "formattedErrors");
        }
    }

    useEffect(() => {
        if (openAddCalendar === false) {
            resetData()
        }
    }, [openAddCalendar])

    const resetData = () => {
        setAddCalendar({
            title: '',
            working_days: '',
            alternative_saturday_off: '',
            week_off: '',
        })
        setErrors({});
        // setSelectedCalendarDatas(' ')
    }


    return (
        <>
            <div className={`offcanvas offcanvas-end creatNewTicketOff ${openAddCalendar ? 'show' : ''}`}

                tabIndex={-1}
                style={{
                    visibility: openAddCalendar ? 'visible' : 'hidden',
                    width: "800px"
                }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{selectedCalendarData ? "Update" : "Create New"} Calendar</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => openAddCalendarModel()}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="table-responsive tableDiv shiftmanagementtbl">
                        <table className="table">
                            <tbody>

                                <tr>
                                    <td width="30%">
                                        <div className="d-flex align-items-center"><span className="srNum">1</span><b
                                            className="ps-3 text-primary"> Title </b></div>
                                    </td>
                                    <td><input type="text" name="title" value={addCalendar.title} onChange={(e) => handleChange(e)} className="form-control" placeholder="Enter Title" />
                                        {errors?.title && (
                                            <CustomError text={errors.title} />
                                        )}
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div className="d-flex align-items-center"><span className="srNum">5</span><b
                                            className="ps-3 text-primary"> Working Days</b></div>
                                    </td>
                                    <td>
                                        <div className="d-flex workingradio">
                                            <div className="shiftInpt">
                                                <select className="form-select" required
                                                    name='shift_type'
                                                    value={workingDay}
                                                    onChange={(e) => setWorkingDayfun(e.target.value)}
                                                >
                                                    <option value='' hidden selected>Select Required Working Days</option>
                                                    <option value='5'>5 Days</option>
                                                    <option value='6'>6 Days</option>
                                                    <option value='0'>7 Days</option>
                                                    <option value='alternative_saturday'>Alternative Saturday Off</option>
                                                </select>
                                                {errors?.working_days && (
                                                    <CustomError text={errors.working_days} />
                                                )}
                                            </div>
                                        </div>
                                    </td>

                                </tr>
                                {workingDay == 'alternative_saturday' &&
                                    <tr>
                                        <td>
                                            <div className="d-flex align-items-center"><span className="srNum">6</span><b
                                                className="ps-3 text-primary">Saturdays</b></div>
                                        </td>
                                        <td>
                                            <div className="d-flex workingradio">
                                                <div className="d-flex gap-2 checkShift">
                                                    <div className="form-check">
                                                        <input className="form-check-input" type="checkbox" value="1" name="1" onClick={alternativeSaturdayFun} checked={alternativeSaturday?.includes("1")} />
                                                        <label className="form-check-label" for="first">
                                                            1st
                                                        </label>
                                                    </div>
                                                    <div className="form-check">
                                                        <input className="form-check-input" type="checkbox" value="2" name="2" onClick={alternativeSaturdayFun} checked={alternativeSaturday?.includes("2")}/>
                                                        <label className="form-check-label" for="second">
                                                            2st
                                                        </label>
                                                    </div>
                                                    <div className="form-check">
                                                        <input className="form-check-input" type="checkbox" value="3" name="3" onClick={alternativeSaturdayFun} checked={alternativeSaturday?.includes("3")}/>
                                                        <label className="form-check-label" for="third">
                                                            3st
                                                        </label>
                                                    </div>
                                                    <div className="form-check">
                                                        <input className="form-check-input" type="checkbox" value="4" name="4" onClick={alternativeSaturdayFun}  checked={alternativeSaturday?.includes("4")}/>
                                                        <label className="form-check-label" for="fourth ">
                                                            4st
                                                        </label>
                                                    </div>
                                                    <div className="form-check">
                                                        <input className="form-check-input" type="checkbox" value="5" name="5" onClick={alternativeSaturdayFun} checked={alternativeSaturday?.includes("5")}/>
                                                        <label className="form-check-label" for="five ">
                                                            5st
                                                        </label>
                                                    </div>
                                                </div>
                                            </div>
                                        </td>

                                    </tr>}
                                {workingDay != '7' && workingDay != 'alternative_saturday' &&
                                    <tr>
                                        <td>
                                            <div className="d-flex align-items-center"><span className="srNum">7</span><b
                                                className="ps-3 text-primary"> Week Off</b></div>
                                        </td>
                                        <td>
                                            <div className="d-flex gap-2 checkShift">
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="1" name='1' id="first" onClick={weekOffFun} checked={weekOff?.includes("1")} />
                                                    <label className="form-check-label" htmlFor="first">
                                                        Monday
                                                    </label>
                                                </div>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="2" name='2' id="second" onClick={weekOffFun} checked={weekOff?.includes("2")} />
                                                    <label className="form-check-label" htmlFor="second">
                                                        Tuesday
                                                    </label>
                                                </div>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="3" name='3' id="third" onClick={weekOffFun} checked={weekOff?.includes("3")} />
                                                    <label className="form-check-label" htmlFor="third">
                                                        Wednesday
                                                    </label>
                                                </div>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="4" name='4' id="fourth " onClick={weekOffFun} checked={weekOff?.includes("4")} />
                                                    <label className="form-check-label" htmlFor="fourth ">
                                                        Thursday
                                                    </label>
                                                </div>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="5" name='5' id="five " onClick={weekOffFun} checked={weekOff?.includes("5")} />
                                                    <label className="form-check-label" htmlFor="five ">
                                                        Friday
                                                    </label>
                                                </div>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="6" name='6' id="five " onClick={weekOffFun} checked={weekOff?.includes("6")} />
                                                    <label className="form-check-label" htmlFor="five ">
                                                        Saturday
                                                    </label>
                                                </div>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value="0" name='0' id="five " onClick={weekOffFun} checked={weekOff?.includes("0")} />
                                                    <label className="form-check-label" htmlFor="five ">
                                                        Sunday
                                                    </label>
                                                </div>
                                            </div>
                                        </td>

                                    </tr>}
                            </tbody>
                        </table>

                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        {/* <button type="button" className="border-0 p-0 bg-transparent cancelBtn">Back</button> */}
                        <button type="button" className="btn btn-primary gradientBg" onClick={() => handleSubmitCreateShiftConfiguration()}>Submit</button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default AddCalendar
