import React from 'react';

const CustomTimePicker = (props) => {
  const {name,value,handleChange,totalHours,intervalMinutes} = props;

  const generateTimeOptions = (totalHours, intervalMinutes) => {
    const timeArray = [];
    let hours = '00';
    let minutes = '00';
    for (let i = 0; i < totalHours; i++) {
      for (let j = 0; j < 60; j++) {
        hours = i < 10 ? `0${i}` : i;
        if (j % intervalMinutes === 0) {
          minutes = j < 10 ? `0${j}` : j;
          timeArray.push({ item: `${hours}:${minutes}` });
        }
      }
    }
    return timeArray;
  };
  const timeOptions = generateTimeOptions(totalHours, intervalMinutes);

  return (
    <select 
      className="form-select" 
      name={name} 
      value={value} 
      onChange={(e) => handleChange(e)}
    >
      <option value='' hidden>HH:MM</option>
      {timeOptions.map((timeObj, index) => (
        <option key={index} value={timeObj.item}>
          {timeObj.item}
        </option>
      ))}
    </select>
  );
}

export default CustomTimePicker;
